class BackToHeader {
    trackScroll() {
        const goTopBtn = document.querySelector('.top');
        const scrolled = window.pageYOffset;
        const coords = document.documentElement.clientHeight;
        if (scrolled > coords) {
            goTopBtn.classList.add('active-up');
        }
        if (scrolled < coords) {
            goTopBtn.classList.remove('active-up');
        }
    }
    backToTop() {
        let timeOut;
        const top = Math.max(document.body.scrollTop, document.documentElement.scrollTop);
        if (top > 0) {
            window.scrollBy(0, -10e5);
            timeOut = setTimeout(this.backToTop, 20);
        }
        else
            clearTimeout((timeOut));
    }
    upHeader() {
        const goTopBtn = document.querySelector('.top');
        window.addEventListener('scroll', () => this.trackScroll());
        goTopBtn.addEventListener('click', () => this.backToTop());
    }
}
export default BackToHeader;
