import { storeUserInfo, storage } from '../controller/storage';
import PreloaderPage from './components/preloader';
const preloaderPage = new PreloaderPage();
class RequestsApi {
    constructor() {
        this.base = 'https://rslang-bak.herokuapp.com';
        this.users = `${this.base}/users`;
        this.signin = `${this.base}/signin`;
        this.words = `${this.base}/words`;
    }
    async createUser(user) {
        const messageError = document.querySelector('.message-error');
        const response = await fetch(`${this.users}`, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(user),
        });
        if (response.status === 417) {
            messageError.textContent = '   e-mail  !   e-mail.';
            messageError.style.display = 'block';
        }
        const content = await response.json();
        return content;
    }
    async loginUser({ email, password }) {
        const messageErrorSignin = document.querySelector('.message-error-signin');
        const response = await fetch(`${this.signin}`, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ email, password }),
        });
        switch (response.status) {
            case 404:
                messageErrorSignin.textContent = '   !    .';
                messageErrorSignin.style.display = 'block';
                break;
            case 403:
                messageErrorSignin.textContent = '  !   .';
                messageErrorSignin.style.display = 'block';
                break;
        }
        const content = await response.json();
        localStorage.setItem('user-info', JSON.stringify(content));
    }
    async getNewUserToken({ userId, refreshToken }) {
        const response = await fetch(`${this.users}/${userId}/tokens`, {
            headers: {
                'Authorization': `Bearer ${refreshToken}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        });
        const content = await response.json();
        storeUserInfo.token = content.token;
        storeUserInfo.refreshToken = content.refreshToken;
        localStorage.setItem('user-info', JSON.stringify(storeUserInfo));
    }
    async getUser({ userId, token }) {
        const response = await fetch(`${this.users}/${userId}`, {
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        });
        const content = await response.json();
        return content;
    }
    async getTextbookWords(group, page) {
        const response = await fetch(`${this.words}?group=${group}&page=${page}`);
        if (storage.currentPage === 'textbook') {
            if (response.ok) {
                preloaderPage.hidePreloaderPage();
            }
        }
        const content = await response.json();
        return content;
    }
    async getGameWord({ userId, token }, wordId) {
        const response = await fetch(`${this.users}/${userId}/words/${wordId}`, {
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        });
        const content = await response.json();
        return content;
    }
    async updateGameWords({ userId, token }, { correct, wrong }, difficultyWord, wordId) {
        const response = await fetch(`${this.users}/${userId}/words/${wordId}`, {
            method: 'PUT',
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                difficulty: difficultyWord,
                optional: { gamesAnswer: { correct: correct, wrong: wrong } },
            }),
        });
        const content = await response.json();
        return content;
    }
    async createGameWords({ userId, token }, { correct, wrong }, difficultyWord, wordId) {
        const response = await fetch(`${this.users}/${userId}/words/${wordId}`, {
            method: 'POST',
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                difficulty: difficultyWord,
                optional: { gamesAnswer: { correct: correct, wrong: wrong } },
            }),
        });
        const content = await response.json();
        return content;
    }
    async getWord({ userId, token }, { wordId }) {
        const response = await fetch(`${this.users}/${userId}/words/${wordId}`, {
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        });
        const content = await response.json();
        return content;
    }
    async createWordsDifficulty({ userId, token }, { wordId }, { correct, wrong }, levelWord) {
        const response = await fetch(`${this.users}/${userId}/words/${wordId}`, {
            method: 'POST',
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                difficulty: levelWord,
                optional: { gamesAnswer: { correct: correct, wrong: wrong } },
            }),
        });
        const content = await response.json();
        return content;
    }
    async updateWordsDifficulty({ userId, token }, { wordId }, { correct, wrong }, levelWord) {
        const response = await fetch(`${this.users}/${userId}/words/${wordId}`, {
            method: 'PUT',
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                difficulty: levelWord,
                optional: { gamesAnswer: { correct: correct, wrong: wrong } },
            }),
        });
        const content = await response.json();
        return content;
    }
    async getTextbookWordsSignupUser({ userId, token }, { groupWords, pageWords }) {
        const response = await fetch(`${this.users}/${userId}/aggregatedWords?wordsPerPage=20&filter={"$and":[{"group":${groupWords}},{"page":${pageWords}}]}`, {
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        });
        if (storage.currentPage === 'textbook') {
            if (response.ok) {
                preloaderPage.hidePreloaderPage();
            }
        }
        const content = await response.json();
        return content;
    }
    async getGameTextbookWordsSignupUser({ userId, token }, { groupWords, pageWords }) {
        const response = await fetch(`${this.users}/${userId}/aggregatedWords?group=${groupWords}&wordsPerPage=20&filter={"$and":[{"$or":[{"userWord.difficulty":"hard"},{"userWord.difficulty":"normal"},{"userWord":null}]},{"page":${pageWords}}]}`, {
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        });
        if (storage.currentPage === 'textbook') {
            if (response.ok) {
                preloaderPage.hidePreloaderPage();
            }
        }
        const content = await response.json();
        return content;
    }
    async getDifficultWordsSignupUser({ userId, token }) {
        const response = await fetch(`${this.users}/${userId}/aggregatedWords?wordsPerPage=3600&filter={"userWord.difficulty":"hard"}`, {
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        });
        if (response.ok) {
            preloaderPage.hidePreloaderPage();
        }
        const content = await response.json();
        return content;
    }
    async getLearnedWordsSignupUser({ userId, token }) {
        const response = await fetch(`${this.users}/${userId}/aggregatedWords?wordsPerPage=3600&filter={"userWord.difficulty":"easy"}`, {
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        });
        if (response.ok) {
            preloaderPage.hidePreloaderPage();
        }
        const content = await response.json();
        return content;
    }
    async getStatistic({ userId, token }) {
        const response = await fetch(`${this.users}/${userId}/statistics`, {
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
        });
        const content = await response.json();
        return content;
    }
    async updateStatistic({ userId, token }, statistic) {
        const response = await fetch(`${this.users}/${userId}/statistics`, {
            method: 'PUT',
            headers: {
                'Authorization': `Bearer ${token}`,
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                learnedWords: 0,
                optional: Object.assign({}, statistic),
            }),
        });
        const content = await response.json();
        return content;
    }
}
export default RequestsApi;
